import {List, SimpleListItem} from '@rmwc/list'
import '@rmwc/list/styles';
import { useEffect } from 'react';

function ListContainer( {schedule, info, periods, day, setMax, setIndex, index} ){
    useEffect(() => setIndex(day - 1), [])
    setMax(4);
    
    console.log(index)
    return (
        <List twoLine >
            {
                
                schedule[index + 1].map((a, b) => {
                    return (
                        <SimpleListItem
                            graphic={info[a].icon}
                            text={a}
                            secondaryText={periods[b]}
                        />
                    )
                })
            }

        </List>
    )
}

export default ListContainer